import axios from 'axios'

const state = {
    images: [],
    loading: true,
    creating: false,
    filter: ''
}

const getters = {
    images: state => {
        if(state.filter === '') return state.images
        else return state.images.filter(image => image.type.includes(state.filter))
    },
    loadingImage: state => state.loading,
    creatingImage: state => state.creating,
    filter: state => state.filter,
}

const actions = {
    fetchImageAction({commit}) {
        axios.get('https://5cee56b11c2baf00142cbdbf.mockapi.io/images').then(result => {
            commit("fetchImage", result.data)
        }).finally(() => commit("loading"))
    },

    deleteImageAction({commit}, id) {
        axios.delete(`https://5cee56b11c2baf00142cbdbf.mockapi.io/images/${id}`).then(result => {
            if(result.status === 200) commit("deleteImage", id)
        })
    },

    filterImageAction({commit}, type) {
        commit("filterImage", type)
    },

    newImageAction({commit}, image) {
        commit("creating", true)
        axios.post('https://5cee56b11c2baf00142cbdbf.mockapi.io/images', image).then(result => {
            if(result.status === 201) commit("newImage", result.data)
        }).finally(() => commit("creating", false))
    }
}

const mutations = {
    fetchImage(state, images) {
        state.images = images
    },
    newImage(state, image) {
        state.images.unshift({id: parseInt(image.id, 10), url: image.url, type: image.type})
        // state.images = [...state.images, image]
    },
    deleteImage(state, id) {
        var index = state.images.findIndex(image => image.id === parseInt(id, 10))
        if(index !== -1){
            state.images.splice(index, 1)
        }
    },
    filterImage(state, type) {
        state.filter = type
    },
    loading(state) {
        state.loading = false
    },
    creating(state, bool) {
        state.creating = bool
    }
}

export default {
  state,
  getters,
  actions,
  mutations
};