<template>
    <tr>
        <td scope="row">{{ index+1 }}</td>
        <td>{{ todo.title }}</td>
        <td><button 
                :class="[todo.completed ? 'btn btn-primary' : ' btn btn-warning' ]"
                @click="editItem(todo)"
            >
                <i v-if="editing" class="fas fa-spinner fa-spin"></i>
                <span v-else>
                    <i class="fas fa-check" v-if="todo.completed"></i>
                    <i class="fas fa-times" v-else></i>
                </span>
            </button></td>
        <td>
            <button class="btn btn-danger" @click="deleteItem(todo.id)">
                <i v-if="deleting" class="fas fa-spinner fa-spin"></i>
                <i v-else class="fas fa-trash"></i>
            </button>
        </td>
    </tr>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
    name: 'TodoItem',
    props: ['todo', 'index'],
    methods: {
        editItem: function(todo) {
            // this.$emit('edit-item', id)
            const updateTodo = {
                id: todo.id,
                title: todo.title,
                completed: !todo.completed,
            }
            this.$store.dispatch('editTodoAction', updateTodo)
        },
        deleteItem: function(id) {
            this.$store.dispatch('deleteTodoAction', id)
        }
    },
    computed: mapGetters(["deleting", "editing"])
}
</script>

<style scoped>
    .btn {
        border-radius: 50%;
        color: white;
    }
    img {
        width: 8em;
        height: 96px;
    }
</style>
