<template>
    <div class="table col-md-8">
        <table class="table">
            <thead class="thead-dark">
                <tr>
                    <th>#</th>
                    <th>Title</th>
                    <th>Completed</th>
                    <th>Control</th>
                </tr>
            </thead>
            <tbody>
                <tr v-if="loading">
                    <td colSpan="4" style="text-align: center">Loading...</td>
                </tr>
                <TodoItem 
                    v-for="(todo, index) in allTodos" 
                    :key="index" 
                    :todo="todo" 
                    :index="index"
                    v-else 
                />
            </tbody>
        </table>
    </div>
</template>

<script>
import TodoItem from './TodoItem'
import { mapGetters } from 'vuex'

export default {
    name: 'TodoList',
    components: {
        TodoItem
    },
    computed: mapGetters(["allTodos", "loading"]),
    created() {
        this.$store.dispatch('fetchTodoAction')
    },
}
</script>