<template>
    <div class="col-md-4">
        <div class="form-input">
            <label class="form_head">Create</label>
            <form @submit.prevent="onCreate">
                <div class="form-group">
                    <label>Title: </label>
                    <input type="text" v-model="title" class="form-control" placeholder="Input title">
                </div>
                <button class="btn btn-primary" type="submit">
                    <i v-if="creating" class="fas fa-spinner fa-spin"></i>
                    <span v-else>Submit</span>
                </button>
            </form>
        </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
    name: "FormInfo",
    data() {
        return {
            title: ''
        }
    },
    methods: {
        onCreate: function() {
            this.$store.dispatch('addTodoAction', this.title)
            this.onClear()
        },
        onClear: function() {
            this.title = ''
        }
    },
    computed: mapGetters(["creating"])
}
</script>