import axios from 'axios'

const state = {
    todos: [],
    loading: true,
    deleting: false,
    editing: false,
    creating: false,
}

const getters = {
    allTodos: state => state.todos,
    loading: state => state.loading,
    deleting: state => state.deleting,
    editing: state => state.editing,
    creating: state => state.creating
}

const actions = {
    fetchTodoAction({commit}) {
        axios.get('https://jsonplaceholder.typicode.com/todos').then(result => {
            commit("fetchTodo", result.data)
        }).finally(() => commit("loading"))
    },

    addTodoAction({commit}, title) {
        commit("creating", true)
        axios.post('https://jsonplaceholder.typicode.com/todos', { title, completed: false }).then(result => {
            commit("newTodo", result.data)
        }).finally(() => commit("creating", false))
    },

    deleteTodoAction({commit}, id) {
        commit("deleting", true)
        axios.delete(`https://jsonplaceholder.typicode.com/todos/${id}`).then(result => {
            if(result.status === 200) commit("deleteTodo", id)
        }).finally(() => commit("deleting", false))
    },

    editTodoAction({commit}, updateTodo) {
        commit("editing", true)
        axios.put(`https://jsonplaceholder.typicode.com/todos/${updateTodo.id}`, updateTodo).then(result => {
            if(result.status === 200) commit("editTodo", updateTodo)
        }).finally(() => commit("editing", false))
    }
}

const mutations = {
    fetchTodo(state, todos) {
        state.todos = todos
    },
    newTodo(state, todo) {
        state.todos.unshift(todo)
    },
    deleteTodo(state, id) {
        var index = state.todos.findIndex(todo => todo.id === id)
        if(index !== -1){
            state.todos.splice(index, 1)
            state.deleting = false
        }
    },
    editTodo: (state, updateTodo) => {
        const index = state.todos.findIndex(todo => todo.id === updateTodo.id);
        if (index !== -1) {
          state.todos.splice(index, 1, updateTodo);
        }
    },
    loading(state) {
        state.loading = false
    },
    deleting(state, bool) {
        state.deleting = bool
    },
    editing(state, bool) {
        state.editing = bool
    },
    creating(state, bool) {
        state.creating = bool
    }
}

export default {
  state,
  getters,
  actions,
  mutations
};